

























import Vue from "vue";
import Component from "vue-class-component";
import axios, { AxiosRequestConfig } from "axios";
import { article } from "../../server/article";
@Component({
  components: {}
})
export default class Editor extends Vue {
  article: any = { editorContent: "" };
  // editorContent = "";
  img_file = {};
  title = "未命名标题";
  imgAdd = (pos, $file) => {
    // 第一步.将图片上传到服务器.
    var formdata = new FormData();
    formdata.append("image", $file);
    this.img_file[pos] = $file;
    axios({
      url: "/edit/uploadimg",
      method: "post",
      data: formdata,
      headers: { "Content-Type": "multipart/form-data" }
    }).then(res => {
      let _res = res.data;
      // 第二步.将返回的url替换到文本原位置![...](0) -> ![...](url)
      (this.$refs.md as any).$img2Url(pos, _res.url);
    });
  };
  imgDel = pos => {
    delete this.img_file[pos];
  };
  publish = () => {
    article.addArticle({
      title: this.title,
      // autor: sessionStorage.getItem("username"),
      content: this.article.editorContent
    });
  };
  saveTitle = () => {
    this.title = (this.$refs["title"] as any).innerText;
  };
}



